import React, { Component } from 'react'
import './App.css';
import ListItems from './ListItems';
import { library } from '@fortawesome/fontawesome-svg-core';
import { faTrash } from '@fortawesome/free-solid-svg-icons';

library.add(faTrash);


class App extends Component {
  constructor(props) {
    super(props)

    this.state = {
      items: [],
      currentItem: {
        text: '',
        key: ''
      }
    }
    // this.handleInput = this.handleInput.bind(this);
  }

  // handleInput(e) {
  //   this.setState({
  //     currentItem: {
  //       text: e.target.value,
  //       key: Date.now()
  //     }
  //   })
  // }
  handleInput = (e) => {
    this.setState({
      currentItem: {
        text: e.target.value,
        key: Date.now()
      }
    })
  }

  addItem = (e) => {
    const st = this.state;
    e.preventDefault();
    const newItem = st.currentItem;
    // alert(newItem);
    // console.log(newItem);

    if (newItem.text !== '') {
      const newItems = [...st.items, newItem];
      this.setState({
        items: newItems,
        currentItem: {
          text: '',
          key: ''
        }
      })
    }

    // this.setState()
  }


  deleteItem = (key) => {
    const st = this.state;
    const filteredItems = st.items.filter(item => item.key !== key);
    this.setState({
      items: filteredItems
    })
  }

  setUpdate = (text, key) => {
    const items = this.state.items;
    items.map(item => {
      if (item.key === key) {
        item.text = text;
      }
    })
    this.setState({
      items: items
    })

  }


  render() {
    return (
      <div className="App">
        <header>
          <form action="" id="to-do-form" onSubmit={this.addItem}>
            <div>
              <input type="text" placeholder="Enter text" value={this.state.currentItem.text} onChange={this.handleInput} />
              <button type="submit">Add</button>
            </div>
          </form>
        </header>
        <ListItems items={this.state.items} deleteItem={this.deleteItem} setUpdate={this.setUpdate} ></ListItems>
      </div>
    )
  }
}

export default App
